import React from 'react';

const themes = {
  initial: {
    color: '#31313f',
    backgroundColor: '#ffffff'
  },
  dark: {
    color: 'white',
    backgroundColor: '#09090b'
  }
};

type Theme = 'initial' | 'dark';

const ThemeContext = React.createContext({ theme: themes.initial, setTheme: () => {} });

const StyleProvider: React.FC = ({ children }) => {
  const [theme, setTheme] = React.useState<Theme>('initial');
  const activeTheme = themes[theme];
  const themeContextValue = React.useRef({ theme: activeTheme, setTheme });

  return (
    <>
      <style jsx global>{`
        @import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap');

        :root {
          overflow-x: hidden;
        }

        body {
          font-family: Open Sans, -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial,
            sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
          font-size: 0.9em;
          margin: 0;
          -webkit-font-smoothing: antialiased;
          padding: 5em;
          box-sizing: border-box;
          color: ${activeTheme.color};
          /* dark: white */
          background-color: ${activeTheme.backgroundColor};
          /* dark: #09090b */
        }

        @media (max-width: 667px) {
          body {
            padding: 2em;
          }
        }

        :root,
        body,
        main,
        #__next {
          height: 100%;
        }
      `}</style>
      <ThemeContext.Provider value={themeContextValue.current}>{children}</ThemeContext.Provider>
    </>
  );
};

export default StyleProvider;
